export class ToDo {
    constructor(title) {
        this.title = title;
        this.dateAdded = new Date();
        this.complete = Math.random() > 0.5;
    }
    toggleComplete() {
        if (this.complete) {
            delete this.dateCompleted;
        }
        else {
            this.dateCompleted = new Date();
        }
        this.complete = !this.complete;
    }
}
